// src/apiConfig.js

// رابط API في بيئة الإنتاج
const PROD_API = 'https://skybridge-flights.onrender.com'; // Render Backend
const CUSTOM_DOMAIN = 'https://skybridgeflights.com';      // الدومين الرسمي

// رابط API في بيئة التطوير
const DEV_API = 'http://localhost:5000';

// تحديد البيئة بناءً على hostname
let API_BASE_URL;
if (process.env.REACT_APP_API_URL) {
  // لو تم تحديد REACT_APP_API_URL في env (مثل Vercel)
  API_BASE_URL = process.env.REACT_APP_API_URL;
} else if (window.location.hostname === 'localhost') {
  // بيئة التطوير
  API_BASE_URL = DEV_API;
} else if (window.location.hostname.includes('skybridgeflights.com')) {
  // عند فتح الموقع من الدومين الرسمي
  API_BASE_URL = PROD_API; 
} else {
  // fallback للإنتاج
  API_BASE_URL = PROD_API;
}

export { API_BASE_URL };

console.log('🔗 API_BASE_URL =', API_BASE_URL);